.card {
  border: 1px solid #f3f3f3;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  display: block;
}

.card__title {
  color: #060b35;
  width: 100%;
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product {
  color: #616070;
  width: 100%;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 16px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 18px;
  margin-top: 24px;
  line-height: 18px;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__link {
  text-align: center;
  text-transform: uppercase;
  color: #f3f3f3;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  display: block;
  bottom: 1px;
}

.card__link:hover {
  color: #00acdc;
  background-color: #f3f3f3;
  border: 1px solid #00acdc;
  border-radius: 5px;
}

.reviews-text {
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.reviews-stars {
  display: flex;
}

.stars-aitem {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  display: flex;
}

.stars-aitem:not(:last-child) {
  margin-right: 4px;
}

.stars-aitem--not-active {
  background-image: url("star.822d561e.svg");
}

* {
  box-sizing: border-box;
  margin: 0;
}

html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}
